@font-face {
  font-family: sans;
  src: url(./font/IRANSans.ttf);
}

body {
  direction: rtl;
}

/* app */
.c-main {
  /* border: 1px solid black !important; */
  display: flex;
  grid-gap: 20px;
  padding: 20px;
  background-color: white;
  max-width: 1440px;
  height: max-content;
  margin: 0 20px 0 0;
  position: relative;
}

.c-right,
.c-left {
  border-radius: 14px;
}

.c-right {
  /* border: 1px solid black; */
  width: 260px;
  height: 670px;
  display: grid;
  justify-content: space-between;
  align-content: space-between;
}

.c-right h2 {
  font-family: sans;
  font-size: 22px;
  font-weight: bold;
  margin: 10px 0;
  color: #e3ab74;
}

.c-left {
  /* border: 1px solid black; */
  width: 80%;
  padding: 0 24px 0;
  background-color: white;
}

.ul-menu {
  display: grid;
  grid-gap: 16px;
  list-style: none;
  padding: 0;
  margin-top: 30px;
}

#li-menu {
  display: flex;
  width: 250px;
  list-style: none;
  border-radius: 8px;
  transition: 0.2s;
  background-color: #3c3c3c1e;
}

#li-menu.active {
  background-color: #005d60;
}

#li-menu.active p {
  color: #ffffff;
}

.icon-menu {
  color: #8b8b8b;
}

.icon-menu {
  margin: 4px 8px;
  font-size: 10px !important;
  padding: 8px;
  width: 24px;
  height: 24px;
  border-radius: 8px;
}

.link {
  text-decoration: none !important;
  display: flex;
  width: 100%;
}

#li-menu p {
  font-family: sans;
  font-size: 16px;
  color: #676767;
  margin: 12px 0 0 0;
}

.logo-menu {
  width: 35%;
  height: 150px;
  display: flex;
  justify-content: center;
  margin: auto;
  /* border: 1px solid black; */
}

.btn {
  display: none !important;
}

.hidden {
  display: none;
}

/* app */

