  height: 22px;
  font-size: 14px;
  margin-right: -15px;
  margin-top: -15px;
  border-radius: 8px;
  color: #005d60;
}

.profile {
  /* border: 1px solid black; */
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  border-radius: 8px;
  background-color: #005d60;
}

.icon-profile {
  color: white;
  font-size: 18px;
}

.Link-profile {
  height: max-content !important;
}

.shop {
  /* border: 1px solid black; */
