  justify-content: center;
  background-color: #005d60;
  border-radius: 8px;
  font-size: 24px;
  align-items: center;
}

.icon-shopping {
  color: white;
  font-size: 18px;
}

/*  */
.menu-foods,
.list-food {
  /* border: 1px solid black; */
  margin: 16px 32px 0;
}

.list-food {
  /* border: 1px solid black; */
  height: max-content;
  overflow-y: scroll;
}

.list-food::-webkit-scrollbar {
  display: none;
}

.menu-foods {
  overflow-x: scroll;
  height: max-content;
  overflow-y: hidden;
}

.menu-foods::-webkit-scrollbar {
  display: none;
}

.ul-menu-foods {
  margin: 0;
  padding: 0;
  display: flex;
  /* grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr; */
