}

.ul-menu-foods li p {
  margin: 0;
  display: flex;
  text-align: center;
  justify-content: center;
  font-family: sans;
  font-size: 12px;
  color: #676767;
}

.sendFood {
  width: 200px !important;
  background-color: rgb(251, 188, 79) !important;
}

.img-fastSent {
  width: 100%;
  height: 80%;
  margin: -4px;
}

.icon-menu-foods {
  width: 48%;
  height: 65%;
  margin: 0 auto 8px;
  display: flex;
  justify-content: center;
}

.link-menu-foods {
  text-decoration: none;
}

.li-menu-foods {
  width: max-content;
  height: max-content;
}

