.container{
    padding: 0;
}
.inset-product{
    /* border: 1px solid black; */
    margin: 28px 0;
    border-radius: 8px;
}
.header-datail{
    /* border: 1px solid black; */
    display: flex;
    margin: 28px 28px 0;
    justify-content: end;
    grid-gap: 8px;
}
.back{
    display: flex;
    grid-gap: 10px;
}
.Details{
    /* border: 1px solid black; */
    margin: 24px;
    overflow: hidden;
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 2fr;
}
/*  */
.c-img-details{
    display: flex;
    justify-content: center;
    align-items: center;
}
.c-img-details{
    animation: slideIn 1s ease 1;
}
.img-Details{
    width: 200px;
    height: 100%;
    margin: auto;
}
.c-info-details{
    animation: slideUp 1s ease 1;
}
.inset-info-details{
    animation: slideUp 1.5s ease 1;
}
.c-info-details > .h3{
    background-color: #005d60;
    width: 80%;
    height: max-content;
    padding: 20px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
}
.c-info-details > .h3 > h3{
    color: #ffffff !important;
    font-family: sans;
    font-size: 16px;
    font-weight: 500;
    margin: 0;
    line-height: 0 !important;
}
.inset-info-details > .a{
    font-family: sans;
    font-size: 16px;
    font-weight: 700;
    color: #2b2b2b;
    margin-right: 8px;
}
.c-detail{
    display: flex;
    grid-gap: 7px;
    margin: 14px 8px 14px 0;
}
.c-detail > span{
    color: rgb(251, 188, 79);
}
.c-detail > a{
    font-family: sans;
    font-size: 14px;
    font-weight: 500;
    color: #454545 !important;
}
.Satisfaction{
    font-family: sans !important;
    font-size: 16px !important;
    font-weight: 700 !important;
    color: #454545 !important;
}
.inset-info-details > p{
    margin:14px  0;
}
.inset-info-details > .description{
    font-size: 14px;
    font-family: sans;
    font-weight: 500;
    margin-right: 8px;
    color: #454545 !important;
}
.c-AddToCart{
    /* border: 1px solid black; */
    display: flex;
    width: max-content;
    grid-gap: 5px;
    align-items: center;
    border-radius: 8px;
    margin-right: 8px;
}
.c-AddToCart > button{
    background-color: #005d60;
    border: none;
    padding: 10px 25px;
    border-radius: 8px;
    font-family: sans;
    font-size: 14px;
    font-weight: 600;
    color: #ffffff;
}
.c-AddToCart > p{
    font-size: 14px;
    color: #e3ab74;
}
.btn-count{
    display: flex;
    align-items: center;
}
.btn-count > button{
    width: max-content;
    height: 40px;
    border-radius: 8px;
    background-color: #005d60;
    color: #ffffff;
    border: none;
    font-family: sans;
    font-size: 18px;
    padding: 20px;
    display: flex;
    align-items: center;
}
.btn-count > a{
    margin: 0 10px;
}
.inset-info-details > .div-price > .pricee{
    margin: 0 0 15px;
    font-family: sans;
    font-size: 16px;
    font-weight: 600;
}
.inset-info-details > .div-price > .pricee > span{
    color: #e3ab74;
}
.div-price{
    display: flex;
    margin: 10px 8px;
    grid-gap: 35px;
}
.div-img-logo{
    display: none !important;
}
.divs-header{
    display: flex;
    grid-gap:8px;
}
/*  */
.btn-add-to-cart-res, .div-price-res, .btn-count-res, .div-only-res{
    display: none ;
}
@media screen and (max-width: 760px){
    .container{
        width: 100%;
    }
    .btn-count-res > button{
        width: 32px;
        height: 32px;
        border: none;
        background-color: #e3ab74;
        border-radius: 4px;
        color: #2b2b2b;
        display: flex;
        align-content:center ;
        justify-content: center;
        padding: 0;
    }
    .btn-count-res{
        display: flex;
        grid-gap: 8px;
        align-items: center;
    }
    .div-img-logo{
        display: block !important;
    }
    .header-datail{
        margin: 15px 10px 0;
        justify-content: space-between;
    }
    .divs-header{
        display: flex;
    }
    .Details{
        /* border: 1px solid black; */
        display: block;
        align-content: space-between;
        margin: 16px 0 70px;
        height: 100%;
        overflow-y: scroll;
    }
    .inset-product{
        margin: 10px 0 0;
        display: block;
    }
    .c-info-details{
        display: block !important;
        margin-top: 20%;
        justify-content: center !important;
        /*text-align: center;*/
    }
    .inset-info-details{
        justify-content: center;
        text-align: center;
        padding: 0 8px;
    }
    .c-detail{
        justify-content: center;
    }
    .inset-info-details > .description{
        margin-right: 0;
    }
    .div-price{
        display: none;
    }
    .div-price-res{
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }
    .c-AddToCart{
        justify-content: space-between;
        width: 100%;
        margin: 0;
        padding: 0;
        display: none;
    }
    .btn-add-to-cart-res{
        display: block;
        margin-top: 8px;
        width: 100%;
        padding: 8px;
        border-radius: 8px;
        border: none;
        background-color: #005d60;
        color: #ffffff;
    }
    .btn-count-res{
        display: flex;
    }
    .btn-plus-res{
        display: flex;
        align-items: center;
        width: 36px !important;
        height: 36px !important;
    }
    .pricee-res-detail{
        font-family: sans;
        font-size: 16px;
        color: #888888;
    }
    .pricee-res-detail > span{
        color: #2b2b2b;
    }
    .c-AddToCart > button{
        width: 100%;
    }
    .img-Details{
        width: 300px;
        /* margin: 0 0 60px; */
    }
    .c-info-details > .h3{
        background-color: white !important;
        width: 100%;
        height: max-content;
        padding: 20px 20px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .c-info-details > .h3 > h3{
        color: #2b2b2b !important;
        font-family: sans;
        font-size: 16px;
        font-weight: bold;
        margin: 0;
        line-height: 0 !important;
    }
}
